// LAYOUT
$mobile-s: 375;
$sm: 576;
$md: 767;
$lg: 992;
$xl: 1200;
$hd: 1440;

// FONTS
$baseFont: "Formular", sans-serif;
$systemFont: -apple-system, 'BlinkMacSystemFont', 'Segoe UI', 'Roboto', 'Helvetica', 'Ubuntu', 'Cantarell', 'Arial', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';

// OPTIONS
$transitionSpeed: .25s;
$ease: ease-in-out;

// COLORS
// Color-names https://htmlcolorcodes.com/color-names/
$fontColor: #313131;
$colorFontGray: #979797;
$colorPrimary: #1BA4DF;
$colorBlue: #2C2B8C;
$colorAccent: #E54323;
$colorYellow: #FCBA27;
$colorGreen: mediumaquamarine;
$colorRed: crimson;
$colorRedHover: lighten($colorRed, 15%);
$borderColor: #EAEAEA;
$colorBg: gainsboro;


// SOCIAL colors
$twitter: #00aced;
$facebook: #3b5998;
$googleplus: #dd4b39;
$pinterest: #cb2027;
$linkedin: #007bb6;
$youtube: #bb0000;
$vimeo: #1ab7ea;
$tumblr: #32506d;
$instagram: #bc2a8d;
$flickr: #ff0084;
$dribbble: #ea4c89;
$quora: #a82400;
$foursquare: #0072b1;
$forrst: #5B9A68;
$vk: #45668e;
$wordpress: #21759b;
$stumbleupon: #EB4823;
$yahoo: #7B0099;
$blogger: #fb8f3d;
$soundcloud: #ff3a00;

// BootStrap 12-col GRID
$gutter: 10px;
$col1: 8.333333%;
$col2: 16.666667%;
$col3: 25%;
$col4: 33.333333%;
$col5: 41.666667%;
$col6: 50%;
$col7: 58.333333%;
$col8: 66.666667%;
$col9: 75%;
$col10: 83.333333%;
$col11: 91.666667%;
$col12: 100%;
