// News
.news {
  padding: 72px 0;
  &__title {
    display: flex;
    align-items: center;
    .h2-title {
      padding-right: 24px; } }
  &__title-cta {
    margin-left: auto; }
  &__slider {
    margin-top: 32px;
    .swiper-slide {
      flex: 0 0 $col3;
      max-width: $col3; } }
  &__cta {
    display: none; } }

@include r($xl) {
  .news {
    padding: 60px 0;
    &__title {
      .h2-title {
        padding-right: 0; } }
    &__slider {
      margin-top: 26px;
      margin-left: -30px;
      margin-right: -30px;
      padding-left: 30px;
      padding-right: 30px;
      .swiper-slide {
        flex: 0 0 265px;
        max-width: 265px; }
      .swiper-navbar {
        display: none; } }
    &__cta {
      margin-top: 30px;
      display: block;
      .btn {
        width: 100%;
        max-width: 350px; } } } }


@include r($lg) {
  .news {
    &__slider {
      .swiper-slide {
        flex: 0 0 225px;
        max-width: 225px; } } } }

@include r($sm) {
  .news {
    &__slider {
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      .swiper-slide {
        flex: 0 0 265px;
        max-width: 265px; } } } }

// newsCard
.newsCard {
  display: block;
  max-width: 320px;
  padding-right: 20px;
  min-width: 1px;
  &__panel {
    display: flex;
    width: 100%;
    max-width: 226px;
    height: 112px; }
  &__date, &__image {
    flex: 0 0 50%;
    max-width: 50%;
    min-width: 1px;
    min-height: 0; }
  &__date {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 24px;
    line-height: 1;
    background: $colorBlue;
    transition: background .25s $ease; }
  &__image {
    position: relative;
    font-size: 0;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      transition: transform .35s ease-out, filter .25s $ease; } }
  &__title {
    margin-top: 26px;
    font-size: 18px;
    line-height: (22 / 18); }
  &:hover {
    .newsCard {
      &__date {
        background: $colorPrimary; }
      &__image {
        img {
          transform: scale(1.075);
          filter: grayscale(1); } } } } }

@include r($lg) {
  .newsCard {
    &__panel {
      max-width: 212px;
      height: 104px; }

    &__title {
      margin-top: 20px;
      font-size: 16px; } } }
