// Events
.events {
  padding: 45px 0 36px;

  &__slider {
    margin-top: 36px;
    .swiper-slide {
      flex: 0 0 $col3;
      max-width: $col3; }
    .eventCard {
      max-width: 336px;
      padding-right: 26px; } } }

@include r($xl) {
  .events {
    padding: 30px 0 50px;
    &__slider {
      margin-top: 30px;
      margin-left: -30px;
      margin-right: -30px;
      padding-left: 30px;
      padding-right: 30px;
      .swiper-slide {
        flex: 0 0 354px;
        max-width: 354px; }
      .swiper-navbar {
        display: none; } } } }

@include r($sm) {
  .events {
    &__slider {
      margin-top: 14px;
      margin-left: -20px;
      margin-right: -20px;
      padding-left: 20px;
      padding-right: 20px;
      // .swiper-slide
      //   flex: 0 0 225px
 } } }      //   max-width: 225px

// eventCard
.eventCard {
  position: relative;
  display: block;
  max-width: 326px;
  padding-right: 20px;
  & + & {
    margin-top: 32px; }
  &__image {
    position: relative;
    z-index: 1;
    font-size: 0;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    overflow: hidden;
    margin-bottom: 12px;
    img {
      width: 100%; } }
  &__tag {
    position: absolute;
    left: -15px;
    top: -15px;
    z-index: 2;
    pointer-events: none; }

  &__year, &__meta {
    font-size: 14px;
    font-weight: 500;
    line-height: 140%;
    color: $colorPrimary; }
  &__date {
    margin-top: 4px;
    font-size: 18px;
    font-weight: 500; }
  &__meta {
    margin-top: 2px; }
  &__location {
    display: flex;
    align-items: center;
    margin: 18px 0px 16px;
    .ico {
      font-size: 24px;
      color: $colorPrimary; }
    span {
      display: inline-block;
      margin-left: 8px;
      font-size: 14px;
      font-weight: 500; } }
  &__row {
    display: flex;
    align-items: center;
    margin-bottom: -18px; }
  &__arrow {
    margin-top: 16px; }
  &__title {
    margin-top: 14px;
    font-size: 18px;
    line-height: (22 / 18);
    font-weight: 500;
    text-transform: uppercase; }
  &__like {
    border: 2px solid #C7C7C7;
    border-radius: 4px;
    display: inline-flex;
    align-items: center;
    padding: 5px 16px 5px 12px;
    margin-left: auto;
    color: #C7C7C7;
    transition: background .25s $ease, border .25s $ease, color .25s $ease;
    span {
      font-size: 14px;
      font-weight: 500; }
    &.is-active {
      background: #FF8269;
      border-color: #FF8269;
      color: white;
      .eventCard__like-icon {
        color: white;
        .ico-mono-like {
          display: block; }
        .ico-mono-like-outline {
          display: none; } } } }

  &__like-icon {
    margin-right: 6px;
    font-size: 0;
    transition: color .25s $ease;
    .ico {
      font-size: 15px; }
    .ico-mono-like {
      display: none; } }

  &:hover {
    .eventCard {
      &__arrow {
        &::before {
          right: 52px; }
        .ico {
          right: 50px; } } } } }

@include r($lg) {
  .eventCard {
    &__title {
      font-size: 16px; } } }

@include r($sm) {
  .eventCard {
    &__location {
      margin-top: 12px;
      margin-bottom: 12px; }
    &__arrow {
      margin-top: 12px; }
    &__title {
      font-size: 14px;
      margin-top: 12px; } } }


