}

.header__language {
  position: relative;
  display: inline-flex;
  align-items: center;
  margin-left: 8px;
  cursor: pointer;
}

.header__language::after {
  display: inline-block;
  content: ' ';
  position: absolute;
  bottom: -2px;
  left: 0;
  right: 0;
  height: 0;
  border-bottom: 2px dashed white;
  transition: right .25s ease;
}

.header__language span {
