}

.header__menu-separator {
  height: 1px;
  background: #1BA4DF;
  margin: 12px 10px;
}

.header__menu {
  flex: 0 1 auto;
  display: flex;
  align-items: center;
  margin: auto -12px -12px 0;
  padding: 0;
}

.header__menu li {
  position: relative;
  z-index: 5;
  display: inline-block;
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  margin-right: 5px;
}

.header__menu li:last-child {
