  border-top-right-radius: 10px;
  transition: background 0.25s ease-in-out;
}

.header__menu li.has-ul:hover {
  background: #2B268E;
}

.header__menu li.has-ul:hover ul {
  pointer-events: all;
  opacity: 1;
}

.header__menu li ul {
  position: absolute;
  z-index: 3;
  top: 100%;
  left: 0;
  min-width: 255px;
  padding: 8px 8px;
  margin: 0;
