// titles
h1, h2, h3, h4, h5, h6 {
  margin: 0; }

.h1-title, .h2-title, .h3-title, .h4-title, .h5-title, .h6-title {
  font-weight: 500; }

.h1-title {
  font-size: 52px; }

.h2-title {
  font-size: 48px; }

.h3-title {
  font-size: 36px; }

.h4-title {
  font-size: 32px; }

.h5-title {
  font-size: 18px; }




