// Greetings
.greetings {
  padding-bottom: 72px;
  &__grid {
    position: relative;
    z-index: 1;
    margin-top: 40px;
    display: flex;
    flex-wrap: wrap;
    background: white;
    color: $fontColor;
    overflow: hidden; }

  &__col {
    flex: 0 0 $col2;
    max-width: $col2;
    display: flex;
    flex-direction: column;
    min-width: 1px;
    min-height: 0;
    border-left: 1px solid #F6F6F6;
    transition: .25s $ease;
    &:first-child {
      border-left: 0; }
    &.is-active {
      flex-basis: $col6;
      max-width: $col6; } } }

@include r($xl) {
  .greetings {
    padding-bottom: 60px;
    &__col {
      flex-basis: 100%;
      max-width: 100%;
      &.is-active {
        flex: 100%;
        max-width: 100%; } } } }

.greetingCard {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  padding: 40px 20px 15px;
  cursor: pointer;
  transition: .25s $ease;
  &__avatar {
    position: relative;
    z-index: 1;
    width: 166px;
    height: 166px;
    border-radius: 50%;
    overflow: hidden;
    font-size: 0;
    transition: .25s $ease;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover; } }
  &__content {
    flex: 1 1 auto;
    max-width: 100%;
    padding: 0;
    transition: .25s $ease; }
  &__text {
    font-size: 18px;
    line-height: 1.3;
    height: 0;
    overflow: hidden;
    transition: .25s $ease;
    p {
      margin: 0;
      & + p {
        margin-top: 1em; } } }
  &__author {
    margin-top: 24px; }
  &__author-name {
    font-weight: bold;
    font-size: 18px;
    line-height: 1.3;
    word-spacing: 100vw; }
  &__author-position {
    font-size: 14px;
    line-height: 1.3;
    color: #8C8C8C; }
  &__arrow {
    position: relative;
    width: 35px;
    height: 15px;
    margin-bottom: 18px;
    color: $colorAccent;
    transition: opacity .25s $ease;
    &::before {
      display: inline-block;
      content: ' ';
      position: absolute;
      top: 50%;
      left: 0;
      right: 2px;
      height: 2px;
      background: $colorAccent;
      transform: translateY(-60%);
      transition: right .25s $ease; }
    .ico {
      position: absolute;
      right: 0;
      top: 0;
      font-size: 15px;
      transition: right .25s $ease; } }

  &.is-active {
    flex-direction: row;
    padding: 0;
    cursor: default;
    .greetingCard {
      &__avatar {
        flex: 0 0 228px;
        width: 228px;
        height: 100%;
        border-radius: 0; }
      &__content {
        padding: 40px 25px; }
      &__text {

        height: auto; }
      &__arrow {
        opacity: 0; } } }
  &:hover {
    .greetingCard {
      &__arrow {
        &::before {
          right: 17px; }
        .ico {
          right: 15px; } } } } }

@include r($xl) {
  .greetingCard {
    flex-direction: row;
    &__avatar {
      flex: 0 0 auto; }
    &__content {
      padding-left: 16px; } } }
